@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.roboto {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1284px;
  margin: 0 auto;
  padding: 0 32px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 320px;
    padding: 0;
  }
}

.nojs .contacts__wrapper, .nojs .footer-navigation__list-container {
  display: block;
  padding-bottom: 20px;
}
.nojs .footer-navigation__title::before, .nojs .footer-navigation__title::after, .nojs .contacts__title::before, .nojs .contacts__title::after {
  display: none;
}
.nojs .info-company__button {
  display: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0.7);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  position: fixed;
  top: 0;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 10;
}
@media (max-width: 1023px) {
  .main-header {
    position: static;
  }
}

.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0 35px;
}
@media (max-width: 1023px) {
  .main-nav {
    padding: 20px 0 25px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    padding: 10px 35px 20px 25px;
  }
}

.main-nav__logo-link {
  margin-right: 13%;
}
@media (max-width: 1023px) {
  .main-nav__logo-link {
    margin: 0;
  }
}

.main-nav__logo {
  display: block;
}
@media (max-width: 767px) {
  .main-nav__logo--visible {
    display: none;
  }
}
.main-nav__logo--phone-visible {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__logo--phone-visible {
    display: block;
  }
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    flex-basis: 15%;
    justify-content: space-between;
    align-self: start;
    flex-grow: 0;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-basis: 37%;
    margin-right: 0;
  }
}

.main-nav__item {
  flex-basis: 36%;
}
.main-nav__item:not(:last-child) {
  margin-right: 4%;
}
@media (max-width: 1023px) {
  .main-nav__item:not(:last-child) {
    margin: 0;
  }
}

.main-nav__mail {
  position: relative;
  font-family: "PT Mono", "Arial", monospace;
  line-height: 18px;
  white-space: nowrap;
  color: #f2f5f7;
}

@media (max-width: 1023px) {
  .main-nav__text {
    display: none;
  }
}

.main-nav__link {
  position: relative;
  display: flex;
  align-items: center;
  font-family: "PT Mono", "Arial", monospace;
  line-height: 18px;
  white-space: nowrap;
  color: #f2f5f7;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    color: #00b3ff;
  }
  .main-nav__link:hover:not(.focus-visible) .main-nav__icon {
    fill: #f2f5f7;
  }
}
.main-nav__link.focus-visible:focus {
  outline: 2px solid #00b3ff;
  border-radius: 1px;
}
.main-nav__link.focus-visible:active, .main-nav__link:active {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .main-nav__link {
    padding: 10px;
  }
}

.main-nav__icon {
  display: block;
  margin-right: 15px;
  fill: #00b3ff;
}
@media (max-width: 1023px) {
  .main-nav__icon {
    margin: 0;
  }
}

.main-nav__button {
  margin-right: 5%;
  padding: 10px 15px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  cursor: pointer;
  user-select: none;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  background-color: transparent;
  color: #f2f5f7;
}
@media (hover: hover) {
  .main-nav__button:hover:not(.focus-visible) {
    color: #00b3ff;
    border: 1px solid #00b3ff;
  }
}
.main-nav__button.focus-visible:focus {
  outline: 2px solid #00b3ff;
}
.main-nav__button.focus-visible:active, .main-nav__button:active {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .main-nav__button {
    display: none;
  }
}

.contacts__container {
  flex-basis: 41%;
}
@media (max-width: 1023px) {
  .contacts__container {
    flex-basis: 48%;
  }
}
@media (max-width: 767px) {
  .contacts__container {
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.contacts__title {
  position: relative;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 767px) {
  .contacts__title {
    padding: 20px 25px;
    margin-bottom: 0;
    cursor: pointer;
  }
  .contacts__title::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 25px;
    height: 1px;
    width: 15px;
    background-color: #c4c4c4;
  }
  .contacts__title::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 25px;
    height: 1px;
    width: 15px;
    background-color: #c4c4c4;
    transform: rotate(90deg);
  }
}

@media (max-width: 767px) {
  .contacts__wrapper {
    display: none;
    padding: 0 20px;
  }
}

.contacts__address {
  display: block;
  margin-bottom: 10px;
  font-family: "PT Mono", "Arial", monospace;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .contacts__address {
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .contacts__address {
    margin-bottom: 15px;
  }
}

.contacts__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.contacts__item:not(:last-child) {
  margin-bottom: 10px;
}

.contacts__link {
  font-family: "PT Mono", "Arial", monospace;
  line-height: 25px;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .contacts__link {
    font-size: 14px;
  }
}
@media (hover: hover) {
  .contacts__link:hover:not(.focus-visible) {
    color: #00b2ff;
    text-decoration: underline;
  }
}
.contacts__link.focus-visible:focus {
  outline: 2px solid #f2f5f7;
}
.contacts__link.focus-visible:active, .contacts__link:active {
  opacity: 0.7;
}

.footer-navigation__container {
  flex-basis: 56%;
  margin-right: 30px;
}
