  }
}
@media (max-width: 767px) {
  .footer-navigation__container {
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.footer-navigation__title {
  position: relative;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
}
