  }
  .footer-navigation__title::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 25px;
    height: 1px;
    width: 15px;
    background-color: #c4c4c4;
  }
  .footer-navigation__title::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 25px;
    height: 1px;
    width: 15px;
    background-color: #c4c4c4;
