.footer-navigation__link {
  display: inline-block;
  font-family: "PT Mono", "Arial", monospace;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 767px) {
  .footer-navigation__link {
    font-size: 14px;
  }
}
@media (hover: hover) {
  .footer-navigation__link:hover:not(.focus-visible) {
    color: #00b2ff;
