.card {
  position: relative;
  width: 100%;
  min-height: 650px;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
  background-color: #3d8fcd;

  &--production {
    background-image: url("../img/background/card-background-production-large.png");

    @media (min-width: $vp-1024) {
      @include retina {
        background-image: url("../../img/background/card-background-production-large@2x.png");
      }
    }

    @include vp-1023 {
      background-image: url("../img/background/card-background-production-medium.png");

      @include retina {
        background-image: url("../../img/background/card-background-production-medium@2x.png");
      }
    }

    @include vp-767 {
      background-image: url("../img/background/card-background-production-small.png");

      @include retina {
        background-image: url("../../img/background/card-background-production-small@2x.png");
      }
    }
  }

  &--assembly {
    background-image: url("../img/background/card-background-assembly-large.png");

    @media (min-width: $vp-1024) {
      @include retina {
        background-image: url("../img/background/card-background-assembly-large@2x.png");
      }
    }

    @include vp-1023 {
      background-image: url("../img/background/card-background-assembly-medium.png");

      @include retina {
        background-image: url("../img/background/card-background-assembly-medium@2x.png");
      }
    }

    @include vp-767 {
      background-image: url("../img/background/card-background-assembly-small.png");

      @include retina {
        background-image: url("../img/background/card-background-assembly-small@2x.png");
      }
    }
  }

  &--accessories {
    background-image: url("../img/background/card-background-accessories-large.png");

    @media (min-width: $vp-1024) {
      @include retina {
        background-image: url("../img/background/card-background-accessories-large@2x.png");
      }
    }

    @include vp-1023 {
      background-image: url("../img/background/card-background-accessories-medium.png");

      @include retina {
        background-image: url("../img/background/card-background-accessories-medium@2x.png");
      }
    }

    @include vp-767 {
      background-image: url("../img/background/card-background-accessories-small.png");

      @include retina {
        background-image: url("../img/background/card-background-accessories-small@2x.png");
      }
    }
  }

  @include hover {
    box-shadow: 0px 0px 36px 0px rgba(0, 181, 232, 0.2);
  }

  @include active {
    opacity: 0.7;
  }

  @include vp-1023 {
    min-height: 250px;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &::after {
    content: "";
    z-index: 1;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(4, 24, 39, 0.5);
  }

  &::before {
    content: "";
    z-index: 3;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 60px;
    height: 60px;
    background-color: $color-deep-sky-blue;
    background-image: url("../../img/svg/arrow-button-card.svg");
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 3px 0px 0px 0px;
  }
}

.card__title {
  z-index: 5;
  position: absolute;
  top: 50px;
  left: 40px;
  width: 50%;
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $color-default-white;

  @include vp-1023 {
    width: 22%;
    top: 40px;
    left: 45px;
  }

  @include vp-767 {
    width: 55%;
    top: 30px;
    left: 30px;
  }
}
